.home-container {
    width: 100%;
    height: 1400px;
    /* border: 8px solid black; */
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #9ab5e1;
    background-image: linear-gradient(315deg, #9ab5e1 0%, #f39fdc 74%);
}

.home-header {
    width: 90%;
    height: 400px;
    /* border: 4px solid grey; */
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 2%;
    background-size: cover;
}

.home-title {
    width: 70%;
    height: 20%;
    /* border: 2px solid blue; */
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(218, 112, 212);
    border-radius: 20px;
}

.about-container {
    width: 70%;
    height: 200px;
    /* border: 2px solid blue; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 2.5%;
    background-color: rgb(237, 183, 234);
    border-radius: 20px;
    transition: transform 0.6s ease;
}

.home-ref {
    width: 88%;
    height: 50%;
    /* border: 2px solid green; */
    margin: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(128, 23, 133);
}

.visit-header {
  width: 30%;
  height: 50px;
  border: 2px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  border-radius: 20px;
  background-color: rgb(218, 112, 212);
}

.visit-container {
    width: 98%;
    border: 1px solid black;
    height: 550px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #8f69e9;
}

.visit-title-down {
    /* border: 2px solid black; */
    width: 100%;
    height: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: opacity 0.19s ease-out;
    opacity: 0;
}

.visit-title-up {
    /* border: 2px solid black; */
    width: 100%;
    height: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: opacity 0.15s ease-out;
    opacity: 0;
}

.visit-text-up {
    /* border: 2px solid purple; */
    padding: 0 10px 12px 10px;
    height: 50%;
}

.click-here {
    /* border: 2px solid black; */
    margin-bottom: 33%;

}

.visit-text-down {
    /* border: 2px solid purple; */
    width: 100%;
    height: 75%;
    padding: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.more-info {
    width: 240px;
    height: 40px;
    /* border: 2px solid blue; */
    opacity: 0;
    z-index: -1;
}

#new-point-1,
#delete-point-1 {
    transition: opacity 0.38s ease;
}

#new-point-2,
#delete-point-2 {
    transition: opacity 0.68s ease;
}
#new-point-3 {
    transition: opacity 0.89s ease;
}
#new-point-4 {
    transition: opacity 1.05s ease;
}

#map-point-1 {
    transition: opacity 0.12s ease;
    margin: 5% 0;
}

#map-point-2 {
    transition: opacity 0.14s ease;
    margin: 5% 0;
}

#map-point-3 {
    transition: opacity 0.14s ease;
    margin: 5% 0;
}

#map-point-4 {
    transition: opacity 0.14s ease;
    margin: 5% 0;
}

.report-box:hover #new-point-1,
.report-box:hover #new-point-2,
.report-box:hover #new-point-3,
.report-box:hover #new-point-4,
.delete-box:hover #delete-point-1,
.delete-box:hover #delete-point-2,
.map-box:hover #map-point-1,
.map-box:hover #map-point-2,
.map-box:hover #map-point-3,
.map-box:hover #map-point-4 {
    opacity: 1;
    z-index: 0;
}

.report-box,
.map-box,
.delete-box,
.update-box {
   width: 70%;
   height: 100px;
   margin: 1%;
   padding: 2% 1.5%;
   display: flex;
   flex-direction: column;
   justify-content: space-evenly;
   align-items: center; 
   border-radius: 20px;
   background-color: rgb(237, 183, 234);
   transition: height 0.23s ease;
}

.report-box:hover,
 .delete-box:hover,
 .map-box:hover,
 .update-box:hover {
    height: 480px;
 }

 
 .report-box:hover .visit-title-down,
 .delete-box:hover .visit-title-down,
 .map-box:hover .visit-title-up,
 .update-box:hover .visit-title-up {
    opacity: 1;
 }

 .map-box:hover .visit-title-up{
     margin-top: 5px;
 }

.report-box,
.delete-box {
    align-self: flex-start;
}

.map-box,
.update-box {
    align-self: flex-end;
}
