@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    display: flex;
    justify-content: center;
}

nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: rgb(128, 23, 133) ;
  color: white;
  width: 100%;
}
.nav-links{
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
.home-container {
    width: 100%;
    height: 1400px;
    /* border: 8px solid black; */
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #9ab5e1;
    background-image: linear-gradient(315deg, #9ab5e1 0%, #f39fdc 74%);
}

.home-header {
    width: 90%;
    height: 400px;
    /* border: 4px solid grey; */
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 2%;
    background-size: cover;
}

.home-title {
    width: 70%;
    height: 20%;
    /* border: 2px solid blue; */
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(218, 112, 212);
    border-radius: 20px;
}

.about-container {
    width: 70%;
    height: 200px;
    /* border: 2px solid blue; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 2.5%;
    background-color: rgb(237, 183, 234);
    border-radius: 20px;
    transition: transform 0.6s ease;
}

.home-ref {
    width: 88%;
    height: 50%;
    /* border: 2px solid green; */
    margin: 1%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(128, 23, 133);
}

.visit-header {
  width: 30%;
  height: 50px;
  border: 2px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  border-radius: 20px;
  background-color: rgb(218, 112, 212);
}

.visit-container {
    width: 98%;
    border: 1px solid black;
    height: 550px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #8f69e9;
}

.visit-title-down {
    /* border: 2px solid black; */
    width: 100%;
    height: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: opacity 0.19s ease-out;
    opacity: 0;
}

.visit-title-up {
    /* border: 2px solid black; */
    width: 100%;
    height: 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: opacity 0.15s ease-out;
    opacity: 0;
}

.visit-text-up {
    /* border: 2px solid purple; */
    padding: 0 10px 12px 10px;
    height: 50%;
}

.click-here {
    /* border: 2px solid black; */
    margin-bottom: 33%;

}

.visit-text-down {
    /* border: 2px solid purple; */
    width: 100%;
    height: 75%;
    padding: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.more-info {
    width: 240px;
    height: 40px;
    /* border: 2px solid blue; */
    opacity: 0;
    z-index: -1;
}

#new-point-1,
#delete-point-1 {
    transition: opacity 0.38s ease;
}

#new-point-2,
#delete-point-2 {
    transition: opacity 0.68s ease;
}
#new-point-3 {
    transition: opacity 0.89s ease;
}
#new-point-4 {
    transition: opacity 1.05s ease;
}

#map-point-1 {
    transition: opacity 0.12s ease;
    margin: 5% 0;
}

#map-point-2 {
    transition: opacity 0.14s ease;
    margin: 5% 0;
}

#map-point-3 {
    transition: opacity 0.14s ease;
    margin: 5% 0;
}

#map-point-4 {
    transition: opacity 0.14s ease;
    margin: 5% 0;
}

.report-box:hover #new-point-1,
.report-box:hover #new-point-2,
.report-box:hover #new-point-3,
.report-box:hover #new-point-4,
.delete-box:hover #delete-point-1,
.delete-box:hover #delete-point-2,
.map-box:hover #map-point-1,
.map-box:hover #map-point-2,
.map-box:hover #map-point-3,
.map-box:hover #map-point-4 {
    opacity: 1;
    z-index: 0;
}

.report-box,
.map-box,
.delete-box,
.update-box {
   width: 70%;
   height: 100px;
   margin: 1%;
   padding: 2% 1.5%;
   display: flex;
   flex-direction: column;
   justify-content: space-evenly;
   align-items: center; 
   border-radius: 20px;
   background-color: rgb(237, 183, 234);
   transition: height 0.23s ease;
}

.report-box:hover,
 .delete-box:hover,
 .map-box:hover,
 .update-box:hover {
    height: 480px;
 }

 
 .report-box:hover .visit-title-down,
 .delete-box:hover .visit-title-down,
 .map-box:hover .visit-title-up,
 .update-box:hover .visit-title-up {
    opacity: 1;
 }

 .map-box:hover .visit-title-up{
     margin-top: 5px;
 }

.report-box,
.delete-box {
    align-self: flex-start;
}

.map-box,
.update-box {
    align-self: flex-end;
}

.new-post-container {
   margin: 0;
   width: 100%;
   height: 1100px;
   border: 2px solid black;
   display: flex;
   justify-content: center;
   background-color: #9ab5e1;
   background-image: linear-gradient(315deg, #9ab5e1 0%, #f39fdc 74%);
   font-family: "Open+sans", 'sans-serif';
}

.new-post-form {
  border: 2px solid black;
  width: 80%;
  height: 1050px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 1.5%;
  background-color: rgb(237, 183, 234);

}

.new-form-group {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* border: 2px solid blue; */
  padding: 2% 2%;
}

.input-label {
    font-size: 24px;
}

.box {
    width: 500px;
    height: 45px;
    font-size: 20px;
    transition: 0.3s width ease;
}

.box:focus {
    width: 600px;
}

.category-box {
    width: 500px;
    height: 45px;
}

.description-box {
   width: 960px;
   height: 150px;
   font-size: 20px;
   transition: 0.3s width ease;
}

.description-box:focus {
    width: 1020px;
}


#counter {
    align-self: flex-end;
}

.button-container {
    padding: 1%;
}

.btn {
    width: 100px;
    height: 50px;
    font-size: 20px;
    background-color: rgb(128, 23, 133);
    color: white;
    transition: transform 0.2s ease;
}

.btn:hover {
    transform: translateY(-8px);
    box-shadow: 0px 8px 6px black;
}

.street-holder {
    width: 100px;
    height: 100px;
    border: 2px solid black;
}
.login-container {
   
   width: 400px;
   height: 400px;
  
   display: flex;
   flex-direction: column;
   align-items: center;
}

.form-container {
   width: 250px;
   height: 250px;
   display: flex;
   
}


.register-container {
   width: 600px;
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
 }
 
 .register-form-container {
    width: 400px;
    height: 400px;
    display: flex;
    justify-content: center;
    
 }

 .register-form-group {
     margin-top: 5%;
     margin-bottom: 5%;
 }
 

 .error-message {
     color: red;
 }
 .post{
   display: flex;
   flex-direction: column;
   align-items: center;
   border-radius: 20px;
   border: 3px solid black;
   width: 95%;
   margin-top: 2%;
} 
.Button-Group{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.upvote{
  max-width: 75px;
  max-height: 50px;
  padding: 10px;
}
.downvote{
  max-width: 75px;
  max-height: 50px;
  transform: rotateX(180deg);
  padding: 10px;
}
.post-footer{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  padding-left: 8%;
}
